import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Icon, Select, Button } from 'antd';

const { Option } = Select;
let id = 0;

@Form.create({ name: 'formSubsidyObject' })
class FormSubsidyObject extends Component {
  state = {
    initialValue: {},
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  remove = (k) => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');
    // We need at least one passenger
    if (keys.length === 0) {
      return;
    }

    // can use data-binding to set
    form.setFieldsValue({
      keys: keys.filter(key => key !== k),
    });
  }

  add = () => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(id += 1);
    // can use data-binding to set
    // important! notify form to detect changes
    form.setFieldsValue({
      keys: nextKeys,
    });
  }

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    const formItemLayouts = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 },
      },
    };
    getFieldDecorator('keys', { initialValue: [0] });
    const keys = getFieldValue('keys');
    console.log('keys', keys);
    const formItems = keys.map((k) => (
      <Fragment>
        <Form.Item
          {...formItemLayouts}
          label='Mã sv'
          key={`${k}-code`}
        >
          {getFieldDecorator(`names[${k}]-code`, {
            validateTrigger: ['onChange', 'onBlur'],
            // rules: [{
            //   required: true,
            //   whitespace: true,
            //   message: "Không được bỏ trống",
            // }],
          })(
            <Fragment>
              <Select style={{ width: '86%', marginRight: 8 }} >
                <Option value="1">14ddc05</Option>
                <Option value="2">14ddc06</Option>
                <Option value="3">14ddc07</Option>
                <Option value="4">14ddc08</Option>
                <Option value="5">14ddc09</Option>
              </Select>
              {keys.length > 1 ? (
                <Icon
                  className="dynamic-delete-button"
                  type="minus-circle-o"
                  disabled={keys.length === 1}
                  onClick={() => this.remove(k)}
                />
              ) : null}
            </Fragment>
          )}
        </Form.Item>
        <Row>
          <Form.Item
            {...formItemLayouts}
            label='Lớp'
            key={`${k}-class`}
          >
            <Row>
              {getFieldDecorator(`names[${k}]-cd`, {
                validateTrigger: ['onChange', 'onBlur'],
                // rules: [{
                //   required: true,
                //   whitespace: true,
                //   message: "Không được bỏ trống",
                // }],
              })(
                <Select style={{ width: '86%', marginRight: 8 }} >
                  <Option value="1">KT1</Option>
                  <Option value="2">KT2</Option>
                  <Option value="3">KT3</Option>
                  <Option value="4">KT4</Option>
                  <Option value="5">KT5</Option>
                </Select>
              )}
            </Row>
          </Form.Item>
        </Row>
        <Form.Item
          {...formItemLayouts}
          label='Đối tượng trợ cấp'
          key={`${k}-object`}
        >
          {getFieldDecorator(`names[${k}]-cdk`, {
            // validateTrigger: ['onChange', 'onBlur'],
            // rules: [{
            //   required: true,
            //   whitespace: true,
            //   message: "Không được bỏ trống",
            // }],
          })(
            <Select style={{ width: '86%', marginRight: 8 }} >
              <Option value="1">SVN Sinh viên thuộc hộ nghèo</Option>
              <Option value="2">GDCS Gia đình chính sách</Option>
              <Option value="3">SVMC Sinh viên bồ côi</Option>
              <Option value="4">SVTB Sinh viên con thương binh</Option>
            </Select>
          )}
        </Form.Item>
      </Fragment>
    ));
    return (
      <Card title='Thêm đối tượng trợ cấp'>
        <Row>
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Hệ" {...formItemLayout}>
              {getFieldDecorator('hệ', {
                initialValue: initialValue.system,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Đại học tính chỉ</Option>
                  <Option value="2">Cao đẳng tính chỉ</Option>
                  <Option value="3">Trung cấp tính chỉ</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khoa" hasFeedback>
              {getFieldDecorator('khoa', {
                initialValue: initialValue.nameRating,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Công nghệ thông tin</Option>
                  <Option value="2">Quản trị kinh doanh</Option>
                  <Option value="3">Điện công nghiệp</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khóa" hasFeedback>
              {getFieldDecorator('khóa', {
                initialValue: initialValue.codeRating,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Khóa 1</Option>
                  <Option value="2">Khóa 2</Option>
                  <Option value="3">Khóa 3</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="CN" hasFeedback>
              {getFieldDecorator('CN', {
                initialValue: initialValue.codeRating,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Cơ khí</Option>
                  <Option value="2">Điện tử</Option>
                  <Option value="3">Kỹ thuật Điện</Option>
                </Select>)}
            </Form.Item>
            {formItems}
            <Form.Item {...formItemLayout} label="Thêm đối tượng">
              <Button type="dashed" onClick={this.add} style={{ width: '100%' }}>
                <Icon type="plus" /> Add field
              </Button>
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card >
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSubsidyObject {...props} />
  </Page>
));



